<template>
  <div>
    <pazzle
      @next='handleNext'
      v-bind="puzzleConfig[level]"
    />
  </div>
</template>

<script>
import pazzle from "./components/Puzzle";
export default {
  components: { pazzle },
  data() {
    return {
      level: 1,
      puzzleConfig: [
        { row: 3, col: 3, img: "./img/1.jpg" },
        { row: 4, col: 4, img: "./img/2.jpg" },
        { row: 5, col: 5, img: "./img/3.jpg" },
        { row: 6, col: 6, img: "./img/4.jpg" },
        { row: 8, col: 8, img: "./img/5.jpg" }
      ]
    };
  },
  methods: {
    handleNext() {
      this.level++;
      if (this.level == this.puzzleConfig.length) {
        alert("最后一关了");
      }
    }
  }
};
</script>

