<template>
  <div
    class='puzzle'
    :style="{width:width +'px',height:height+'px'}"
  >
    <div
      class="puzzle-block"
      v-for="(item,index) in puzzleRandomArr"
      :key='item.id'
      :style="{
        width: bWidth +'px',
        height:bWidth+ 'px',
        top:item.y+'px',
        left:item.x+'px',
        background:`url(${img}) -${puzzleArr[index].x}px -${puzzleArr[index].y}px` ,
        opacity:index === puzzleRandomArr.length-1 && 0
        }"
      @click='handleClick'
      :ref="index === puzzleRandomArr.length -1 ? 'empty' : 'block'"
      :data-relX='puzzleArr[index].x'
      :data-relY='puzzleArr[index].y'
    >

    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      default: 500
    },
    height: {
      type: Number,
      default: 500
    },
    row: {
      type: Number,
      default: 3
    },
    col: {
      type: Number,
      default: 3
    },
    img: {
      type: String
    }
  },
  data() {
    return {};
  },
  methods: {
    handleClick(e) {
      const blockDom = e.target;
      const emptyDom = this.$refs.empty[0];
      if (!this.isJustise(blockDom, emptyDom)) return;
      let { top, left } = blockDom.style;
      blockDom.style.left = emptyDom.style.left;
      blockDom.style.top = emptyDom.style.top;
      emptyDom.style.left = left;
      emptyDom.style.top = top;
      if (this.win()) {
        setTimeout(() => {
          alert("win");
          this.gotoNext();
        }, 400);
      }
    },
    gotoNext() {
      this.$emit("next");
    },
    isJustise(blockDom, emptyDom) {
      let flag = false;
      let { top: bTop, left: bLeft } = blockDom.style;
      let { top: eTop, left: eLeft } = emptyDom.style;
      let lDis = Math.abs(parseFloat(bLeft) - parseFloat(eLeft));
      let tDis = Math.abs(parseFloat(bTop) - parseFloat(eTop));
      flag =
        (bTop === eTop && Math.floor(lDis) == parseInt(this.bWidth)) ||
        (bLeft === eLeft && Math.floor(tDis) == parseInt(this.bWidth));

      return flag;
    },
    win() {
      let blockDom = this.$refs.block;

      return blockDom.every(dom => {
        let { top, left } = dom.style;
        let { relx, rely } = dom.dataset;
        const flag =
          parseInt(left) == parseInt(relx) && parseInt(top) == parseInt(rely);

        return flag;
      });
    }
  },
  computed: {
    bWidth() {
      return this.width / this.col;
    },
    puzzleArr() {
      let arr = [];
      let { row, col, bWidth } = this;
      for (let i = 0; i < row; i++) {
        for (let j = 0; j < col; j++) {
          let item = {
            x: i * bWidth,
            y: j * bWidth,
            id: new Date().toLocaleString + Math.random() * 100
          };
          arr.push(item);
        }
      }

      return arr;
    },
    puzzleRandomArr() {
      let randomArr = [...this.puzzleArr];
      const lastEle = randomArr[randomArr.length - 1];
      randomArr.length = randomArr.length - 1;
      randomArr.sort(() => Math.random() - 0.5);
      randomArr.push(lastEle);
      return randomArr;
    }
  }
};
</script>

<style>
.puzzle {
  border: 2px solid #ddd;
  position: relative;
}
.puzzle-block {
  background-color: aquamarine;
  position: absolute;
  border: 1px solid #ddd;
  box-sizing: border-box;
  transition: all 0.3s linear;
}
</style>